import changeWebApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        code3: 0
    },
    mutations: {
        setData3(state, payload) {
            Object.assign(state,{code3:payload.code})
            // console.log(payload);
            
        }
    },
    actions: {
        async changeWebServerAsync(context, payload) {  
            const data= await changeWebApi.changeWebServer({
                serverId: payload.serverId,
                webserverId: payload.webserverId,
                runningState: payload.runningState
            })
            context.commit("setData3", data)
            // console.log(data);
        },
    }
}