<template>
  <div class="baseContainer">
    <div class="box1">
      <el-form style="margin-left:30px;">
        <el-form-item label>
          <button @click="getText">确认修改</button>
        </el-form-item>
        <el-form-item label="蜜罐服务器名称：">
          <input type="text" placeholder="服务器AAA" class="first" v-model="mytexy" />
        </el-form-item>
        <el-form-item label="备注：">
          <input class="elinp" type="text" placeholder />
        </el-form-item>
      </el-form>
    </div>
    <div class="box2">
      <div class="box2-1">
        <h2>服务器硬件与网络属性</h2>
        <el-table ref="multipleTable" :data="list1" tooltip-effect="dark" style="width: 100%">
          <el-table-column fixed prop="cpu" label="CPU"></el-table-column>
          <el-table-column prop="hardDisk" label="内存"></el-table-column>
          <el-table-column prop="ram" label="硬盘"></el-table-column>
          <el-table-column prop="ip4Addr" label="IP地址×1"></el-table-column>
          <el-table-column prop="ip6Addr" label="IPv6地址"></el-table-column>
        </el-table>
      </div>
      <div class="box2-2">
        <h2>服务器所部署服务</h2>
        <el-table ref="multipleTable" :data="list2" tooltip-effect="dark" style="width: 100%">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="serverType" label="服务器类型"></el-table-column>
          <el-table-column prop="deployState" label="部署状态"></el-table-column>
          <el-table-column prop="runningState" label="运行状态"></el-table-column>
          <el-table-column prop="installDir" label="安装目录"></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapActions, mapState } = createNamespacedHelpers("baseNature");
export default {
  data() {
    return {
      mytexy: ""
    };
  },

  mounted() {
    this.serverId = JSON.parse(sessionStorage.getItem("webID")).id;
    this.id = JSON.parse(sessionStorage.getItem("webID")).id;
    // this.serverName = JSON.parse(sessionStorage.getItem("webID")).name;
    this.findDeployAsync({
      serverId: this.serverId
    });
    this.findAttributeAsync({
      serverId: this.serverId
    });
  },
  computed: {
    ...mapState(["code", "data", "list1", "list2", "serverName"])
  },
  methods: {
    ...mapActions(["findDeployAsync", "findAttributeAsync", "updateNameAsync"]),
    getText() {
      this.updateNameAsync({
        id: this.id,
        serverName: this.mytexy
      });
    }
  }
};
</script>

<style scoped>
.baseContainer {
  display: flex;
  flex-direction: column;
}
.box1 {
  width: 100%;
  height: 220px;
  background-color: #ffffff;
  border-bottom: 1px solid #d8d8d8;
}
.box1 button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  color: #ffffff;
  background-color: #22a5f7;
  width: 90px;
  height: 32px;
  border-radius: 4px;
  margin: 30px 30px 0px 0;
}
.box1 button:hover {
  cursor: pointer;
}
.el-form-item input {
  background-color: #ffffff;
  height: 32px;
  width: 500px;
  padding-left: 10px;
}
.first {
  margin-left: 27px;
}
.box2 {
  width: 100%;
  height: 400px;
  background-color: #ffffff;
  display: flex;
  justify-content: space-around;
}
.box2-1 {
  width: 800px;
  height: 100%;
}
.box2-2 {
  width: 700px;
  height: 100%;
}
h2 {
  font-size: 16px;
  margin-top: 20px;
}
.el-table td {
  background-color: #e8e8e8;
}
.elinp {
  margin-left: 97px;
}
</style>
