import nodeListApi from "../services/nodeListServices";

export default {
    namespaced: true,
    state: {
        pageNum: 1,
        pageSize: 1,
        list:[],
        total:0
    },
    mutations: {
        setData(state,payload){            
           Object.assign(state,payload)
        },
        changePage(state,payload){
            Object.assign(state,{
                ...state,
                pageNum:payload
            })
         }
    },
    actions: {
        async nodeListAsync(context,payload) {
            const {data} = await nodeListApi.nodeList(context.state)
            context.commit("setData",data)
            // console.log(data);
            
        },
        async changePageAsync(context,payload){
            context.commit("changePage",payload)
            // console.log(payload);
         
            const {data} = await nodeListApi.nodeList(context.state)
            context.commit("setData",data)
        }
    }
}