import changeFlowApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        code4: 0
    },
    mutations: {
        setData4(state, payload) {
            // console.log(payload);
            Object.assign(state,{code4:payload.code})
        }
    },
    actions: {
        async changeFlowServerAsync(context, payload) {
            const data= await changeFlowApi.changeFlowServer({
                serverId: payload.id,
                id: payload.id,
                runningState: payload.runningState
            })
            context.commit("setData4", data)
        },
    }
}