<template>
  <div>
    <el-table :data="list">
      <el-table-column prop="id" label="蜜罐服务器ID"></el-table-column>
      <el-table-column prop="name" label="名称"></el-table-column>
      <el-table-column prop="connStatus" label="服务器状态" :formatter="changeConnStatus"></el-table-column>
      <el-table-column prop="currentsupport" label="暗网服务" :formatter="changeCurrentsupport"></el-table-column>
      <el-table-column prop="runningState" label="暗网运行状态" :formatter="changeRunningState"></el-table-column>
      <el-table-column prop="system" label="操作系统"></el-table-column>
      <el-table-column prop="host" label="IP地址"></el-table-column>
      <el-table-column prop="local" label="所在地"></el-table-column>
      <el-table-column prop="serverType" label="部署诱导服务" :formatter="changeServerType"></el-table-column>
      <el-table-column prop="attackManagerStatus" label="运行状态" :formatter="changeAttack"></el-table-column>
      <el-table-column fixed="right" label="操作" width="120">
        <template slot-scope="scope">
          <el-button @click="detali(scope.row)" type="text" size="small">详情</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="total"
      :page-size="pageSize"
      :current-page="pageNum"
      @current-change="handleCurrentChange"
    ></el-pagination>
  </div>
</template>

<style scoped>
.el-pagination {
  text-align: center;
  margin-top: 15px;
}
</style>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapActions, mapState } = createNamespacedHelpers("serList");
export default {
  mounted() {
    this.serchListAsync();
  },
  data() {
    return {};
  },
  computed: {
    ...mapState(["pageNum", "pageSize", "list", "total"])
  },
  methods: {
    ...mapActions(["serchListAsync", "changePageAsync"]),
    detali(prop) {
      sessionStorage.setItem("webID", JSON.stringify(prop));
      location.hash = "#/Info/serveDetails";
      // this.$router.push({
      //   path: "/Info/serveDetails",
      //   query: {
      //     id: prop.id,
      //     name: prop.name,
      //     runningState: prop.runningState
      //   }
      // });
    },
    handleCurrentChange(val) {
      this.changePageAsync(val);
    },
    changeConnStatus(row, column) {
      switch (row.connStatus) {
        case 0:
          return "在线";
          break;
        case 1:
          return "离线";
          break;
      }
    },
    changeCurrentsupport(row, column) {
      switch (row.currentsupport) {
        case "0":
          return "TOR";
          break;
        case "1":
          return "12P";
          break;
        case "2":
          return "ZeroNet";
          break;
      }
    },
    changeRunningState(row, column) {
      switch (row.runningState) {
        case 0:
          return "运行";
          break;
        case 1:
          return "停止";
          break;
      }
    },
    changeServerType(row, column) {
      switch (row.serverType) {
        case 0:
          return "论坛";
          break;
        case 1:
          return "blog";
          break;
        case 2:
          return "网页";
          break;
      }
    },
    changeAttack(row, column) {
      switch (row.attackManagerStatus) {
        case 0:
          return "运行";
          break;
        case 1:
          return "停止";
          break;
      }
    },
  }
};
</script>