import findAttackApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        attCode: 0,
        attData: {
            
        }
    },
    mutations: {
        findAttack(state, payload) {//自带两个参数state, payload   
            Object.assign(state,{
                attCode:payload.code,
                attData:payload.data
            })//把data整合进5行state中   
            // console.log(payload);
        }
    },
    actions: {
        async findAttackServerAsync(context, payload) {  //自带两个参数state, payload
            const data = await findAttackApi.findAttackS({serverId:payload})//data返回的数据请求到的数据，然后18行data数据就是13行payload数据
            context.commit("findAttack", data)//把返回的数据传入12行findWeb方法中
        },
    }
}