import chooseNodeApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        code6: 0
    },
    mutations: {
        setData6(state, payload) {
            // console.log(payload);
            Object.assign(state,{code6:payload.code})
        }
    },
    actions: {
        async chooseNodeServerAsync(context, payload) {
            const data= await chooseNodeApi.chooseNodeServer({
                serverId: payload.id,
                id: payload.id,
                currentsupport: payload.currentsupport
            })
            context.commit("setData6", data)
        },
    }
}