import changeAttackApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        code5: 0
    },
    mutations: {
        setData5(state, payload) {
            Object.assign(state,{code5:payload.code});
            // console.log(payload);
        }
        
    },
    actions: {
        async changeAttackServerAsync(context, payload) {
            // console.log(payload);
            
            const data= await changeAttackApi.changeAttackServer({
                serverId: payload.serverId,
                attckId: payload.attckId,
                runningState: payload.runningState
            })//对象名和值一样，可以把这三个参数简写为payload
            context.commit("setData5", data)
        },
    }
}