import findFlowApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        flowCode: 0,
        flowData: {
            
        }
    },
    mutations: {
        findFlow(state, payload) {//自带两个参数state, payload   
            Object.assign(state,{
                flowData:payload.data,
                flowCode:payload.data
            })//把data整合进5行state中   
            // console.log(payload);
            
        }
    },
    actions: {
        async findFlowAsync(context, payload) {  //自带两个参数state, payload
            
            const data = await findFlowApi.findFlowSetting({serverId:payload})//data返回的数据请求到的数据，然后18行data数据就是13行payload数据
            context.commit("findFlow", data)//把返回的数据传入12行findWeb方法中

        },
    }
}