import findWebApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        webCode: 0,
        webData: {
            
        }
    },
    mutations: {
        findWeb(state, payload) {//自带两个参数state, payload   
            Object.assign(state,{
                webData:payload.data,
                webCode:payload.data
            })//把data整合进5行state中   

        }
    },
    actions: {
        async findWebServerAsync(context, payload) {  //自带两个参数state, payload
            
            const data = await findWebApi.findWebServer({serverId:payload})//data返回的数据请求到的数据，然后18行data数据就是13行payload数据
            context.commit("findWeb", data)//把返回的数据传入12行findWeb方法中

        },
    }
}