import findNodeApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        code: 0,
        data: { }
    },
    mutations: {
        findNode(state, payload) {//自带两个参数state, payload            
            Object.assign(state, payload,payload.data)//把data整合进5行state中
        }
    },
    actions: {
        async findNodeServerAsync(context, payload) {  //自带两个参数context, payload
            const data = await findNodeApi.findNodeServer({serverId:payload})//data返回的数据请求到的数据，然后18行data数据就是13行payload数据
            context.commit("findNode", data)//把返回的数据传入12行findNode方法中         
        },
    }
}