import axios from "axios";
import qs from 'qs';

export default {
    //登录
    async login(params) {
        let 
            data
         = await axios.post("/aw/user/login", qs.stringify(params))
        return data;
    },
    //获取验证码
    async authCode(params) {
        const data = await axios.get("aw/user/code", {
                responseType: "arraybuffer"
            })
            .then(res => 'data:image/png;base64,' + btoa(
                new Uint8Array(res.data).reduce((data, byte) => data + String.fromCharCode(byte), "")
            ))
        return data;
    },
}