<template>
  <div class="manageContainer">
    <div class="controller">
      <span>启动控制：</span>
      <el-switch v-model="value1" active-color="#13ce66" inactive-color="#ff4949" @change="handleChange"></el-switch>
      <i>已启动</i>
      <button>保存</button>
    </div>
    <div class="box1">
      <div class="box1-1">
        <el-form :label-position="labelPosition" label-width="140px" style="margin-left:20px;">
          <span>基础服务</span>
          <el-form-item label="控制服务状态：">
            <i>在线</i>
            <i>离线</i>
          </el-form-item>
          <el-form-item label="控制状态：">
            <i>可控</i>
            <i>不可控</i>
          </el-form-item>
          <el-form-item label="备注：">
            <input type="text" placeholder="服务A" class="detailInput" />
          </el-form-item>
          <el-form-item label>
            <button>更新</button>
          </el-form-item>
        </el-form>
      </div>
      <div class="box1-2">
        <el-form label-width="180px" style="margin-left:20px;">
          <span>蜜罐暗网节点服务</span>
          <el-form-item label="蜜罐服务暗网节点状态：">
            <i>运行</i>
            <el-switch v-model="value2" active-color="#ff4949" inactive-color="#13ce66" v-if="flag"></el-switch>
            <el-switch
              v-model="value2"
              active-color="#ff4949"
              inactive-color="#13ce66"
              disabled
              v-else
            ></el-switch>
            <i>停止</i>
          </el-form-item>
          <el-form-item label="蜜罐节点支持暗网：">
            <i>{{chooseNS}}</i>
          </el-form-item>
          <el-form-item label>
            <el-radio-group v-model="radio1" ref="radio">
              <el-radio label="TOR"></el-radio>
              <el-radio label="12P"></el-radio>
              <el-radio label="ZeroNet"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label>
            <button @click="darkNet">切换暗网</button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="box2">
      <div class="box2-1">
        <el-form label-width="180px" style="margin-left:20px;" ref="form">
          <span>蜜罐web管理服务</span>
          <el-form-item label="蜜罐web服务状态：">
            <i>运行</i>
            <el-switch
              v-model="value3"
              active-color="#ff4949"
              inactive-color="#13ce66"
              v-if="flag"
            />
            <el-switch
              v-model="value3"
              active-color="#ff4949"
              inactive-color="#13ce66"
              disabled
              v-else
            />
            <i>停止</i>
          </el-form-item>
          <el-form-item label="蜜罐web服务类型：">
            <el-select v-model="form.region" placeholder="当前服务类型">
              <el-option label="论坛" value="论坛"></el-option>
              <el-option label="博客" value="博客"></el-option>
              <el-option label="网页" value="网页"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="服务含有外挂程序：">
            <el-radio-group v-model="radio1" ref="radio1">
              <el-radio :label="1">含有</el-radio>
              <el-radio :label="2">未含</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
      </div>
      <div class="box2-2">
        <el-form label-width="180px" style="margin-left:20px;">
          <span>蜜罐流量获取服务</span>
          <el-form-item label="流量获取服务状态：">
            <i>运行</i>
            <el-switch v-model="value4" active-color="#ff4949" inactive-color="#13ce66" v-if="flag"></el-switch>
            <el-switch
              v-model="value4"
              active-color="#ff4949"
              inactive-color="#13ce66"
              disabled
              v-else
            ></el-switch>

            <i>停止</i>
          </el-form-item>
          <el-form-item label="流量回传方式：">
            <el-radio-group v-model="radio2">
              <el-radio :label="1">
                定时
                <i>每1小时传回1次</i>
              </el-radio>
              <el-radio :label="2">
                定量
                <i>每300M回传一次</i>
              </el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="本地存储容量：">
            <input type="text" :placeholder="flowData.capacity" />
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="box3">
      <el-form label-width="180px" style="margin-left:20px;">
        <span>蜜罐服务攻击管理</span>
        <div class="box3-0">
          <i>启动攻击服务</i>
          <el-switch v-model="value5" active-color="#ff4949" inactive-color="#13ce66" v-if="flag"></el-switch>
          <el-switch v-model="value5" active-color="#ff4949" inactive-color="#13ce66" disabled v-else></el-switch>

          <i>停止攻击服务</i>
        </div>
        <div class="box3-1">
          <el-checkbox>自动搜索</el-checkbox>
          <i>自动搜索相应位置并发送攻击指令</i>
        </div>
        <div class="box3-1">
          <el-checkbox>指定位置</el-checkbox>
          <i>攻击地址：</i>
          <input type="text" placeholder="192.168.1.1" />
        </div>
        <div class="box3-1">
          <el-checkbox>指定范围</el-checkbox>
          <i>攻击范围：</i>
          <input type="text" placeholder="192.168.1.1~192.168.1.100" />
        </div>
        <div class="box3-2">
          <button>确认</button>
        </div>
      </el-form>
    </div>
    <div class="box4">
      <span>当前接入节点（10）</span>
      <el-table ref="multipleTable" :data="list" tooltip-effect="dark" style="width: 100%">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="flowId" label="节点"></el-table-column>
        <el-table-column prop="ip_addr" label="IP地址"></el-table-column>
        <el-table-column prop="join_status" label="接入状态"></el-table-column>
        <el-table-column prop="node_source" label="节点来源"></el-table-column>
        <el-table-column prop="input_flow" label="接入流量"></el-table-column>
        <el-table-column prop="out_flow" label="转出流量"></el-table-column>
        <el-table-column prop="reconn_count" label="连接次数"></el-table-column>
        <el-table-column prop="total_flow" label="历史总流量"></el-table-column>
        <el-table-column fixed="right" label="操作" width="120">
          <template>
            <el-button type="text" size="small">下载流量</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="pageSize"
        :current-page="pageNum"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const {
  mapActions: fnodeActions,
  mapState: fnodeState3
} = createNamespacedHelpers("findNodeServer");
const { mapActions: actions2, mapState: state2 } = createNamespacedHelpers(
  "changeNodeServer"
);
const { mapActions: actions3, mapState: state3 } = createNamespacedHelpers(
  "changeWebServer"
);
const {
  mapActions: chooseNodeA,
  mapState: chooseNodeS
} = createNamespacedHelpers("chooseNodeServer");
const { mapActions: webServeA, mapState: webServeS } = createNamespacedHelpers(
  "findWebServer"
);
const { mapActions: flowA, mapState: flowS } = createNamespacedHelpers(
  "findFlowSetting"
);
const {
  mapActions: changeflowA,
  mapState: changeflowS
} = createNamespacedHelpers("changeFlowServer");

const { mapActions: attServeA, mapState: attServeS } = createNamespacedHelpers(
  "findAttackServer"
);

const {
  mapActions: changeAttA,
  mapState: changeAttAS
} = createNamespacedHelpers("changeAttackServer");

const { mapActions: nodeInfoA, mapState: nodeInfoS } = createNamespacedHelpers(
  "findNodeInfoS"
);
//开启/关闭蜜罐服务器连接
const { mapActions: statusA, mapState: statusS } = createNamespacedHelpers(
  "changeServerStatus"
);
export default {
  data() {
    return {
      value1: true,
      value2: true,
      value3: true,
      value4: true,
      value5: true,
      labelPosition: "left",
      formLabelAlign: {
        name: "",
        region: "",
        type: ""
      },
      checkList: ["选中且禁用", "复选框 A"],
      radio1: 1,
      radio2: 2,
      multipleSelection: [],
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      chooseNS: "TOR/12P/ZeroNet",
      serverId: "",
      disabled: false,
      flag: true
    };
  },
  async mounted() {
    this.serverId = JSON.parse(sessionStorage.getItem("webID")).id;

    let id = JSON.parse(sessionStorage.getItem("webID")).id;

    let runningState = JSON.parse(sessionStorage.getItem("webID")).runningState;

    let webserverId = JSON.parse(sessionStorage.getItem("webID")).id;

    let status = JSON.parse(sessionStorage.getItem("webID"))
      .attackManagerStatus;

    //第一部分
    //暗网节点服务查询
    await this.findNodeServerAsync(this.serverId);

    //改变暗网节点开启(关闭)状态
    await this.changeNodeServerAsync({
      serverId: this.data.serverId,
      id: this.data.id,
      runningState: this.data.runningState
    });
    // console.log(this.data);
    // console.log(this.code2);
    this.switch2();

    //切换暗网服务
    await this.chooseNodeServerAsync({
      serverId: this.data.serverId,
      id: this.data.id,
      currentsupport: this.data.currentsupport
    });

    //第二部分
    //查询蜜罐服务器web服务

    await this.findWebServerAsync(this.serverId);

    //改变暗网Web服务的开启(关闭)状态
    await this.changeWebServerAsync({
      serverId: this.webData[0].serverId,
      webserverId: this.webData[0].id,
      runningState: this.webData[0].runningState
    });
    this.switch3();
    this.rad1();

    //第三部分
    //查询流量回传设置
    await this.findFlowAsync(this.serverId);
    // console.log(this.flowData);

    //切换流量服务运行状态
    await this.changeFlowServerAsync({
      serverId: this.flowData.serverId,
      id: this.flowData.id,
      runningState: this.flowData.runningState
    });
    this.switch4();

    //第四部分
    //攻击服务配置信息查询
    await this.findAttackServerAsync(this.serverId);
    // console.log(this.attData);

    //开启或关闭攻击服务

    await this.changeAttackServerAsync({
      serverId: this.attData.serverId,
      attckId: this.attData.attackWay,
      runningState: this.attData.runningState
    });
    // console.log(this.code5);
    this.switch5();

    //查询蜜罐服务器下的节点列表
    await this.findNodeInfoAsync({ serverId: this.serverId });

    //开启/关闭蜜罐服务器连接
    await this.changeStatusAsync({
      serverId: this.serverId,
      status
    });
    // console.log(status);
  },
  computed: {
    ...fnodeState3(["code", "data"]),
    ...state2(["code2"]),
    ...state3(["code3"]),
    ...chooseNodeS(["code6"]),
    ...webServeS(["webCode", "webData"]),
    ...flowS(["flowCode", "flowData"]),
    ...changeflowS(["code4"]),
    ...attServeS(["attCode", "attData"]),
    ...changeAttAS(["code5"]),
    ...nodeInfoS(["pageNum", "pageSize", "list", "total", "nodeCode"]),
    ...statusS(["code0"])
  },
  methods: {
    ...fnodeActions(["findNodeServerAsync"]),
    ...actions2(["changeNodeServerAsync"]),
    ...actions3(["changeWebServerAsync"]),
    ...chooseNodeA(["chooseNodeServerAsync"]),
    ...webServeA(["findWebServerAsync"]),
    ...flowA(["findFlowAsync"]),
    ...changeflowA(["changeFlowServerAsync"]),
    ...attServeA(["findAttackServerAsync"]),
    ...changeAttA(["changeAttackServerAsync"]),
    ...nodeInfoA(["findNodeInfoAsync", "changePageAsync"]),
    ...statusA(["changeStatusAsync"]),

    switch2() {
      if (this.code2 == 200) {
        this.value2 = true;
      } else {
        this.value2 = false;
      }
    },
    switch3() {
      if (this.code3 == 200) {
        this.value3 = false;
      } else {
        this.value3 = true;
      }
    },
    switch4() {
      if (this.code4 == 200) {
        this.value4 = false;
      } else {
        this.value4 = true;
      }
    },
    switch5() {
      if (this.code5 == 200) {
        this.value5 = false;
      } else {
        this.value5 = true;
      }
    },
    darkNet() {
      if (this.code6 == 200) {
        this.chooseNS = this.$refs.radio.value;
      }
      // console.log(this.$refs.radio.value);
    },
    rad1() {
      if (this.webData[0].hasPlugin == 1) {
        this.webData[0].hasPlugin = this.$refs.radio1.value;
      } else {
        this.$refs.radio1.value = 10;
      }
    },
    handleCurrentChange(val) {
      this.changePageAsync({ pageNum: val, serverId: this.serverId });
    },
    handleChange() {
        this.flag=!this.flag;
        if (this.code0==200) {
          this.value1=false
        }else{
          this.value1=true
        }
    }
  }
};
</script>

<style scoped>
