}
.box1-2 .el-form-item i:nth-of-type(1) {
  margin-right: 30px;
}
.box1-2 .el-form-item i:nth-of-type(2) {
  margin-left: 30px;
}
.box2 > div {
  width: 800px;
  height: 240px;
  margin-top: 30px;
  background-color: #ffffff;
}
.box2 span {
  font-size: 16px;
  color: #333333;
  font-weight: 600;
  display: inline-block;
  margin: 15px 0;
}
.box2-1 .el-form-item i:nth-of-type(1) {
  margin-right: 30px;
}
.box2-1 .el-form-item i:nth-of-type(2) {
  margin-left: 30px;
}
.el-radio__label > i {
  margin-left: 15px;
  color: #999999;
}
.box2-2 .el-form-item i:nth-of-type(1) {
  margin-right: 30px;
}
.box2-2 .el-form-item i:nth-of-type(2) {
  margin-left: 30px;
}
.box2-2 input {
  background-color: #ffffff;
  height: 32px;
  width: 440px;
  padding-left: 10px;
}
.box3 {
  width: 100%;
  height: 360px;
  margin-top: 30px;
  background-color: #ffffff;
}
.box3 span {
  font-size: 16px;
  color: #333333;
  font-weight: 600;
  display: inline-block;
  margin: 15px 0;
}
.box3-0 {
  font-size: 14px;
  color: #666666;
}
.box3-0 i:nth-of-type(1) {
  margin-right: 30px;
}
.box3-0 i:nth-of-type(2) {
  margin-left: 30px;
}
.box3 input {
  background-color: #ffffff;
  height: 32px;
  width: 580px;
  padding-left: 10px;
}
.box3-1 {
  margin: 35px 0;
  font-size: 14px;
  color: #666666;
}
