  display: inline-block;
  margin: 15px 0 15px 20px;
}
.el-pagination {
  margin: 30px auto;
  text-align: right;
}
.box3-2 > button {
  border: 1px solid #d9d9d9;
  outline: none;
  background-color: #ffffff;
