<template>
  <div class="detailContainer">
    <div class="detailTit">
      服务器列表>
      <i>详情</i>
    </div>
    <div class="detailAll">
      <span>服务器ID：{{idInput}}</span>
      <span>名称：{{nameInput}}</span>
      <span>
        状态：
        <i>{{runningState}}</i>
      </span>
    </div>
    <div class="functionZone">
      <span @click="manageC" >管理控制</span>
      <span @click="fileM">文件管理</span>
      <span @click="baseN">基本属性</span>
      <span @click="runR">运行日志</span>
      <span @click="crawler">爬虫</span>
    </div>
    <div class="detailBody">
      <router-view />
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const {mapState}=createNamespacedHelpers("baseNature");
export default {
  data() {
    return {}
  },
  computed:{
    idInput () {
      // let save=JSON.parse(sessionStorage.getItem("webID"));
      return JSON.parse(sessionStorage.getItem("webID")).id
    },
    nameInput(){
      return JSON.parse(sessionStorage.getItem("webID")).name
    },
    runningState(){
      return JSON.parse(sessionStorage.getItem("webID")).runningState
    },
    ...mapState(["serverName"])
  },

  methods: {
  
    manageC() {
      location.hash = "#/Info/serveDetails";
      
    },
    fileM() {
      location.hash = "#/Info/serveDetails/fileManage";
    },
    baseN() {
      location.hash = "#/Info/serveDetails/baseNature";
    },
    runR() {
      location.hash = "#/Info/serveDetails/runRecord";
    },
    crawler(){
      location.hash = "#/Info/serveDetails/reptile";
    }
  }
};
</script>

<style scoped>
.detailContainer {
  display: flex;
  flex-direction: column;
}
.detailTit {
  font-size: 14px;
  color: #666666;
  padding-bottom: 30px;
  padding-left: 20px;
}
i {
  font-style: normal;
}
.detailTit > i {
  font-size: 18px;
  color: #333333;
  font-weight: 600;
}
.detailAll {
  border: none;
  background-color: #ffffff;
  display: flex;
}
.detailAll > span {
  font-size: 18px;
  color: #333333;
  padding: 25px;
  font-weight: 600;
}
.detailAll > span i {
  color: #36d068;
}
.functionZone {
  display: flex;
  border-bottom: 1px solid #d8d8d8;
  padding-left: 20px;
}
.functionZone > span {
  font-size: 16px;
  padding: 20px;
}
.functionZone > span:hover{
  cursor: pointer;
}
</style>