import baseNatureApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        code: 0,
        data: {},
        list1: [],
        list2: [],
        serverName:"123"
    },
    mutations: {
        deploy(state, payload) {
            Object.assign(state, {
                list2: payload
            })
        },
        attribute(state, payload) {
            Object.assign(state, {
                list1: payload
            })
        },
        updateName(state, payload) {
            Object.assign(state, {
                serverName: payload
            })
        },

    },
    actions: {
        //挂载服务查询
        async findDeployAsync(context, payload) {
            const {
                data
            } = await baseNatureApi.findDeploy({
                serverId: payload.serverId
            })
            context.commit("deploy", data)
            
        },

        //查询服务器属性
        async findAttributeAsync(context, payload) {
            const {
                data
            } = await baseNatureApi.findAttribute({
                serverId: payload.serverId
            })
            context.commit("attribute", data)
        },

        //修改蜜罐服务器名称
        async updateNameAsync({commit}, payload) {
            const data = await baseNatureApi.updateName(payload);
            if(data.code === 200){
                commit("updateName",payload.serverName)
            }
        },
    }
}