import userApi from "../services/userServices";

export default {
    namespaced: true,
    state: {
        message: "",
        code: "",
    },
    mutations: {
        setLogin(state, payload) {
            Object.assign(state, payload)
        }
    },
    actions: {
        async loginAsync(context, payload) {
            const
                {data} = await userApi.login(payload);
            context.commit("setLogin", data)
        },
        async authCodeAsync(context, payload) {
            const data = await userApi.authCode(context.state);
            return data;
        },
    }
}