import serchListApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        pageNum: 1,
        pageSize: 1,
        list:[],
        total:0
    },
    mutations: {
        setData(state,payload){            
           Object.assign(state,payload)  
                    
        },
        changePage(state,payload){
            Object.assign(state,{
                ...state,
                pageNum:payload
            })
         }
    },
    actions: {
        async serchListAsync(context,payload) {
            const {data} = await serchListApi.serchList(context.state)
            context.commit("setData",data)            
        },
        async changePageAsync(context,payload){
            context.commit("changePage",payload)            
            const {data} = await serchListApi.serchList(context.state)
            context.commit("setData",data)
        }
    }
}