import changeStatusApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        code0: 0
    },
    mutations: {
        setData0(state, payload) {
            Object.assign(state,{code0:payload.code})
            
        }
    },
    actions: {
        async changeStatusAsync(context, payload) {  
            const data= await changeStatusApi.changeStatus({
                serverId: payload.serverId,
                status: payload.status,
            })
            context.commit("setData0", data)
        },
    }
}