import findNodeInfoSApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        pageNum: 1,//页数
        pageSize: 1,//每一页显示条数
        list: [],//返回每一页数据
        total: 3,//总页数
        nodeCode: 0,

    },
    mutations: {
        valData(state, payload) {
            Object.assign(state, {
                list: payload.list,
                nodeCode: payload.code,
                total:payload.total
            })
        },
        changePage(state, payload) {
            Object.assign(state, {
                ...state,
                pageNum: payload
            })
        }
    },
    actions: {
        async findNodeInfoAsync(context, payload) {
            const {
                data
            } = await findNodeInfoSApi.findNodeInfo({
                ...context.state,
                serverId: payload.serverId
            })
            context.commit("valData", data)
        },//第一次获取的数据
        async changePageAsync(context, payload) {
            const {pageNum,serverId}=payload
            const {pageSize}=context.state//
            const {
                data
            } = await findNodeInfoSApi.findNodeInfo({pageNum,serverId,pageSize})
            context.commit("valData", data)
        }//分页时获取的数据
    }
}