<template>
  <div>
    <el-table :data="list">
      <el-table-column prop="node_id" label="节点编号"></el-table-column>
      <el-table-column prop="currentSupport" label="节点服务" :formatter="changeCurrentSupport"></el-table-column>
      <el-table-column prop="ip_addr" label="IP地址"></el-table-column>
      <el-table-column prop="running_state" label="运行状态" :formatter="changeRunning_state"></el-table-column>
      <el-table-column prop="total_flow" label="流量"></el-table-column>
      <el-table-column prop="reconn_count" label="连接数"></el-table-column>
      <el-table-column prop="handle" label="操作">
        <template>
          <el-button @click="detali" type="text" size="small">重启</el-button>
          <el-button @click="detali" type="text" size="small">开启</el-button>
          <el-button @click="detali" type="text" size="small">关闭</el-button>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="详情" width="120">
        <template slot-scope="scope">
          <el-button @click="detali(scope.row)" type="text" size="small">详情</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="total"
      :page-size="pageSize"
      :current-page="pageNum"
      @current-change="handleCurrentChange"
    ></el-pagination>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapActions, mapState } = createNamespacedHelpers("nodeList");
export default {
  mounted() {
    this.nodeListAsync();
  },
  data() {
    return {};
  },
  computed: {
    ...mapState(["pageNum", "pageSize", "list", "total"])
  },
  methods: {
    ...mapActions(["nodeListAsync", "changePageAsync"]),
    detali(prop) {
      sessionStorage.setItem("nodeID", JSON.stringify(prop));
      location.hash = "#/Info/nodeDetails";
    },
    handleCurrentChange(val) {
      this.changePageAsync(val);
    },
    changeCurrentSupport(row, column) {
      switch (row.currentSupport) {
        case 0:
          return "TOR";
          break;
        case 1:
          return "12P";
          break;
        case 2:
          return "ZeroNet";
          break;
      }
    },
    changeRunning_state(row, column) {
      switch (row.running_state) {
        case 0:
          return "运行中";
          break;
        case 1:
          return "已停止";
          break;
        case 2:
          return "重启中";
          break;
      }
    },
  }
};
</script>


<style scoped>
.el-pagination {
  text-align: center;
  margin-top: 15px;
}
</style>