<template>
  <div class="logContainer">
    <span>服务器运行日志</span>
    <div id="res" style=" "></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      path: "ws://192.168.1.112:8081/log/web",
      socket: ""
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (typeof WebSocket === "undefined") {
        alert("您的浏览器不支持socket");
      } else {
        // 实例化socket
        this.socket = new WebSocket(this.path);
        // 监听socket连接
        this.socket.onopen = this.open;
        // 监听socket错误信息
        this.socket.onerror = this.error;
        // 监听socket消息
        this.socket.onmessage = this.getMessage;
      }
    },
    open() {
      console.log("socket连接成功");
    },
    error() {
      console.log("连接错误");
    },
    getMessage(msg) {
      let response = document.getElementById("res");
      response.innerHTML += "<p>" + msg.data + "</p>";
      this.data = msg.data;
      console.log(this.data);
    },
    close() {
      console.log("socket已经关闭");
    },

    destroyed() {
      // 销毁监听
      this.socket.onclose = this.close;
    }
  }
};
</script>

<style scoped>
.logContainer {
  width: 100%;
  height: 650px;
  background-color: #ffffff;
}
#res {
  width: 100%;
  height: 600px;
  overflow-y: auto;
  margin-left: 30px;
  color: red;
  font-size: 14px;
  margin-left: 30px;
  overflow-y: auto;
}
span {
  margin: 15px 30px 10px 30px;
  font-size: 18px;
  font-weight: 600;
  display: inline-block;
}
.res > p {
  margin-left: 30px;
}
</style>