import changeNodeApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        code2: 0
    },
    mutations: {
        setData2(state, payload) {
            // console.log(payload);
            Object.assign(state,{code2:payload.code})
        }
    },
    actions: {
        async changeNodeServerAsync(context, payload) {
            const data= await changeNodeApi.changeNodeServer({
                serverId: payload.id,
                id: payload.id,
                runningState: payload.runningState
            })            
            context.commit("setData2", data)
            // console.log(data);
            
        },
    }
}