<template>
  <div class="loginBox">
    <div class="login">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span class="name">AW系统登陆</span>
        </div>
        <div class="inputbox">
          <div class="box1">
            <span class="user"></span>
            <el-input
              class="formItem input"
              placeholder="请输入用户名"
              style="{width:200px;height:40px; } "
              v-model="user.username"
            />
          </div>
          <div class="box2">
            <span class="password"></span>
            <el-input
              class="formItem input"
              placeholder="请输入密码"
              type="password"
              v-model="user.password"
            />
          </div>
          <div class="authCode">
            <el-input
              class="auth"
              placeholder="请输入验证码"
              type="text"
              v-model="user.authCode"
              style="{padding-left:40px; } "
            />
            <div>
              <img :src="imgUrl" class="code" @click="refreshCode" />
            </div>
          </div>
          <div class="formItem">
            <el-button type="primary" style="width:100%" class="loginbtn" @click="handleClick">立即登录</el-button>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("user");
export default {
  data() {
    return {
      user: {
        username: "",
        password: "",
        authCode: ""
      },
      imgUrl: ""
    };
  },
  created() {
    let urlTemp = "assets/code.png";
  },
  mounted() {
    this.getCode();
  },
  updated() {
    // this.handleClick();
  },
  computed: {
    ...mapState(["message", "code"])
  },
  methods: {
    async handleClick() {
      await this.loginAsync({
        username: this.user.username,
        password: this.user.password,
        code: this.user.authCode
      });
      if (this.code===200) {
        this.$router.push(`/Info`);
        this.$message("登录成功");
      } else if(this.code===50002) {
        this.$message("账号或密码错误，请重新输入");
      }else if(this.code===50004){
        this.$message("验证码错误，请重新输入");
      }
    },
    async getCode() {
      const img = await this.authCodeAsync();
      this.imgUrl = img;
    },
    ...mapActions(["authCodeAsync", "loginAsync"]),
    refreshCode() {
      this.getCode();
    }
  }
};
</script>

<style>
input.el-input__inner {
  outline: none;
  width: 100px;
  height: 40px;
  border: none;
  background-color: #eee;
  padding: 0;
}
input.el-input__inner::-webkit-input-placeholder {
  font-size: 16px;
  color: #283443;
}
.el-card__header {
  border: none;
}
</style>

<style scoped>
.loginBox {
  width: 100%;
  height: 100%;
  background-color: #2d3a4b;
  color: #eee;
  overflow-y: hidden;
}
.login {
  width: 100%;
  height: 100%;
  margin: 200px auto;
}
.name {
  font-size: 30px;
  font-weight: 600;
  margin: 0 auto;
  color: #eee;
}
.clearfix {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}
.box-card {
  position: relative;
  width: 520px;
  max-width: 100%;
  padding: 0 35px 0;
  margin: 0 auto;
  overflow: hidden;
  background-color: #2d3a4b;
  border: none;
}

.loginbtn {
  height: 40px;
  background-color: #1890ff;
  border-radius: 5px;
  color: #eee;
  border: 1px solid transparent;
  outline: none;
  font-size: 18px;
  margin-top: 30px;
}
.user {
  background-image: url("../assets/user.png");
  width: 24px;
  height: 24px;
  display: inline-block;
  background-size: 24px auto;
  background-position: center center;
  background-repeat: no-repeat;
  margin-right: 15px;
}
.password {
  background-image: url("../assets/password.png");
  width: 24px;
  height: 24px;
  display: inline-block;
  background-size: 24px auto;
  background-position: center center;
  background-repeat: no-repeat;
  margin-right: 15px;
}
.box1,
.box2 {
  display: flex;
  align-items: center;
  border: 1px solid;
  margin-top: 30px;
  background-color: #eee;
}
.input {
  height: 36px;
  width: 360px;
  padding: 5px;
  display: inline-block;
  line-height: 36px;
  background-color: #eee;
}
.authCode {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
}
.auth {
  height: 36px;
  width: 340px;
  padding: 5px;
  display: inline-block;
  line-height: 36px;
  background-color: #eee;
}
.code {
  width: 90px;
  height: 44px;
}
.code:hover {
  cursor: pointer;
}
</style>
